import React, { Component } from "react";
import Styles from "./index.module.css";
import Modal from "../../component/modal";
import Border from "../../component/border";
import { Toast } from "antd-mobile";
class Index extends Component {
  state = {
    selectAmount: 0
  };
  // 选择金额方式
  selectAmount = selectAmount => {
    return () => {
      this.setState(
        {
          selectAmount
        },
        () => {
          if (selectAmount === 0) {
            this.onOk();
          }
        }
      );
    };
  };
  //监听输入金额
  watchAmount = e => {
    let { value = "" } = e.target;
    if (value.length > 0) {
      this.setState({
        amount: value,
        showCloseIcon: true
      });
    } else {
      this.setState({
        amount: value,
        showCloseIcon: false
      });
    }
  };
  //清除金额
  clearPhone = () => {
    this.setState({
      amount: "",
      showCloseIcon: false
    });
  };
  closeAmount = () => {
    if (this.props.closeAmount) {
      this.props.closeAmount();
    }
  };
  onOk = () => {
    if (this.state.amount <= this.props.remainingBalance) {
      this.props.onOk({
        payType: this.state.selectAmount,
        amount: this.state.amount
      });
    } else if (this.state.selectAmount === 1) {
      Toast.info(
        `The input amount should be less than the total amount$${this.props.remainingBalance}`,
        3
      );
    }
  };
  render() {
    return (
      <div>
        {/* 选择支付金额 */}
        {this.props.visibleAmount && (
          <Modal hideBtn close={this.closeAmount} className={Styles.modal}>
            <div className={Styles.payment_modal}>
              <div
                className={`${Styles.line_select} ${
                  this.state.selectAmount === 0 ? Styles.active : ""
                }`}
                onClick={this.selectAmount(0)}
              >
                <i className={Styles.sign_icon} />
                <span>All the remaining balance</span>
              </div>
              <div style={{ marginLeft: "0.28rem" }}>
                <Border />
              </div>
              <div
                className={`${Styles.line_select} ${
                  this.state.selectAmount === 1 ? Styles.active : ""
                }`}
                onClick={this.selectAmount(1)}
              >
                <i className={Styles.sign_icon} />
                <span>Split the bill and pay the following amount</span>
              </div>
              {this.state.selectAmount === 1 && (
                <div className={Styles.email}>
                  <input
                    type="text"
                    value={this.state.amount}
                    onChange={this.watchAmount}
                    placeholder="Minimum: $10"
                  />
                  {this.state.showCloseIcon ? (
                    <i
                      className={Styles.field_close}
                      onClick={this.clearPhone}
                    />
                  ) : (
                    <i className={Styles.field_edit} />
                  )}
                  {this.state.amount >= 10 ? (
                    <span className={Styles.active} onClick={this.onOk}>
                      Confirm
                    </span>
                  ) : (
                    <span>Confirm</span>
                  )}
                </div>
              )}
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

export default Index;
