import React, { Component } from "react";
import Styles from "./index.module.css";
class Index extends Component {
  constructor(props) {
    super(props);
    this.timeout = null;
  }
  state = {
    visible: true,
    seconds: this.props.seconds || 10
  };
  componentDidMount() {
    // 控制组件是否需要倒计时关闭
    if (!this.props.hideSeconds) {
      this.timeServer();
    }
  }
  closeModal = () => {
    if (this.props.close) {
      this.props.close();
    }
  };
  timeServer = () => {
    this.timeout = setTimeout(() => {
      if (this.state.seconds === 0) {
        this.timeout = null;
        if (this.props.onOk) {
          // 倒计时为0时自动执行
          this.onOk();
        }
        return false;
      }
      this.setState({
        seconds: this.state.seconds - 1
      });
      this.timeServer();
    }, 1000);
  };
  onOk = () => {
    this.props.close();
    this.props.onOk();
  };
  render() {
    let className = this.props.className;
    return (
      <div className={Styles.modal} onClick={this.closeModal}>
        <div className={Styles.modal_body}>
          <div
            onClick={e => {
              e.stopPropagation();
            }}
            className={`${Styles.modal_content} ${className ? className : ""}`}
          >
            {/* title */}
            {this.props.title && (
              <p className={Styles.modal_title}>{this.props.title}</p>
            )}
            {/* info */}
            {this.props.content && (
              <p className={Styles.modal_info}>{this.props.content}</p>
            )}
            {this.props.children}
            {/* btn */}
            {!this.props.hideBtn && (
              <div className={Styles.modal_btn} onClick={this.props.onOk}>
                {this.props.btnText ? this.props.btnText : "Sure"}
                {!this.props.hideSeconds && (
                  <span>（{this.state.seconds}s）</span>
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default Index;
