import React, { Component } from "react";
import Styles from "./index.module.css";
import Line from "../../component/line";
import SelectAmount from "./selectAmount";
import View from "../../component/view";
import { connect } from "dva";
import Params from "../../tools/param";
import {
  times,
  plus,
  round
} from "../../tools/number-precision";
import SelectRate from "./selectRate";
import BankCard from "../bankCard";
// 高阶组件 统一link跳转
import HocLink from "../../component/link";
import { Toast } from "antd-mobile";
import { minus } from "../../tools/number-precision";
class Index extends Component {
  constructor(props) {
    super(props);
    // 从url中解析出storeId
    let orderId = Params.get("orderId", window.location.href);
    this.state = {
      useBankCard: true,
      visibleAmount: false,
      selectAmount: 0,
      orderId,
      tableNumber: localStorage.tableNumber,
      storeId: localStorage.storeId,
      scanDishes: [],
      cardInfo: {},
      bankInfo: {},
      orderInfo: {}
    };
  }
  componentWillMount() {
    this.props.showLoading();
    this.getBusinessInfo(this.state.storeId);

    this.scanOrder({
      tableNumber: this.state.tableNumber,
      storeId: this.state.storeId
    });
    this.scanDishes({
      tableNumber: this.state.tableNumber,
      storeId: this.state.storeId
    });
  }

  // 获取商家信息
  getBusinessInfo(storeId) {
    this.props.dispatch({
      type: "orders/getBusinessInfo",
      payload: {
        storeId
      }
    });
  }
  //获取生成的订单
  scanOrder = params => {
    this.props
      .dispatch({
        type: "orderInfo/scanOrder",
        payload: {
          ...params
        }
      })
      .then(data => {
        if (data.totalAmount) {
          this.setState({
            orderInfo: {
              ...data,
              payAmount: data.remainingBalance
            }
          });
        }
      });
  };
  scanDishes = params => {
    this.props
      .dispatch({
        type: "orderInfo/scanDishes",
        payload: {
          ...params
        }
      })
      .then(data => {
        this.props.hideLoading();
        this.setState({
          scanDishes: data
        });
      });
  };
  // 切换modal
  toggleModal = status => {
    return () => {
      this.setState({
        [status]: !this.state[status]
      });
    };
  };
  // close amount
  closeAmount = () => {
    this.toggleModal("visibleAmount")();
  };
  // 关闭选择的小费比例
  closeRate = () => {
    this.toggleModal("visibleRate")();
  };
  // change tips
  changeTips = (tipRate = 0) => {
    return () => {
      this.setState(
        {
          orderInfo: {
            ...this.state.orderInfo,
            tipRate
          }
        },
        () => {
          this.calcTotal();
        }
      );
    };
  };
  // 选择的小费费率
  hasSelectRate = tipNumber => {
    let tip = tipNumber / 100;
    this.changeTips(tip)();
    this.closeRate();
  };
  // 选择的支付方式或者支付部分金额
  hasSelectAmount = ({ payType, amount }) => {
    if (payType === 0) {
      this.setState(
        {
          orderInfo: {
            ...this.state.orderInfo,
            payAmount: this.state.orderInfo.remainingBalance
          }
        },
        () => {
          this.calcTotal();
        }
      );
    } else {
      this.setState(
        {
          orderInfo: {
            ...this.state.orderInfo,
            payAmount: amount
          }
        },
        () => {
          this.calcTotal();
        }
      );
    }
    this.closeAmount();
  };
  // calcTotal
  calcTotal = () => {
    let { orderInfo } = this.state;
    let tipsPrice = round(times(orderInfo.tipRate, orderInfo.payAmount), 2);
    this.setState({
      orderInfo,
      tipsPrice
    });
  };
  checkOrderConfirm = () => {
    const { orderInfo, bankInfo } = this.state;
    const {
      orderId,
      subTotal,
      tax,
      totalAmount,
      payAmount,
      tipRate = 0,
      tip
    } = orderInfo;
    const { cardNumber, expMonth, expYear, cvs } = bankInfo;
    // 先校验是否有银行卡信息
    if (this.validBankInfo()) {
      // 在线支付
      this.payments({
        orderId,
        subTotal,
        tax,
        tipRate,
        total: totalAmount,
        payAmount,
        tip,
        cardNumber,
        expMonth,
        expYear,
        cvs
      });
    } else if (this.state.useBankCard === false) {
      // 线下支付
      this.offlinePay({
        orderId,
        subTotal,
        tax,
        total: totalAmount,
        payAmount: payAmount,
        tip,
        isOfflinePay: true
      });
    }
  };
  // 在线支付
  payments = params => {
    this.props
      .dispatch({
        type: "paymentPreview/payments",
        payload: {
          ...params
        }
      })
      .then(data => {
        // 支付成功，跳转到支付成功页面
        if (data.code === 200) {
          this.props.dispatch({
            type: "paymentPreview/printCheckout",
            payload: {
              orderId: this.state.orderId
            }
          });
          // 支付成功，跳转到支付成功页面
          this.props.hocLink(`#/paymentResult?orderId=${this.state.orderId}`)();
        } else {
          Toast.info(data.message || "error", 2);
        }
      });
  };
  // 线下支付
  offlinePay = params => {
    this.props
      .dispatch({
        type: "paymentPreview/offlinePay",
        payload: {
          ...params
        }
      })
      .then(data => {
        if (data.code === 200) {
          this.props.dispatch({
            type: "paymentPreview/printCheckout",
            payload: {
              orderId: this.state.orderId
            }
          });
          // 支付成功，跳转到支付成功页面
          this.props.hocLink(`#/paymentResult?orderId=${this.state.orderId}`)();
        } else {
          Toast.info(data.message || "error", 2);
        }
      });
  };
  // 支付是否使用银行卡
  bankCard = useBankCard => {
    return () => {
      this.setState({
        useBankCard
      });
    };
  };
  // bankInfo
  bankInfo = bankInfo => {
    this.setState({
      bankInfo
    });
  };
  validBankInfo = () => {
    if (this.state.useBankCard) {
      const { cardNumber, expMonth, expYear, cvs } = this.state.bankInfo;
      if (!cardNumber) {
        Toast.info("Please enter your card number", 2);
        return false;
      } else if (!cvs) {
        Toast.info("Please enter your card name", 2);
        return false;
      } else if (!expMonth) {
        Toast.info("Please enter your expMonth", 2);
        return false;
      } else if (!expYear) {
        Toast.info("Please enter your expYear", 2);
        return false;
      }

      return true;
    }
  };
  render() {
    const { businessInfo = {} } = this.props.orders;
    const { scanDishes = [], orderInfo = {} } = this.state;
    const buildCard = () => {
      return scanDishes.map((item, index) => {
        const { foodItem } = item;
        return (
          <div className={`${Styles.card} ${Styles.card_spe}`} key={index}>
            <div className={Styles.card_info}>
              <span>{foodItem.name}</span>
              <p>
                {foodItem.dailySpecialPrice ? (
                  <span>${foodItem.dailySpecialPrice}</span>
                ) : (
                  <span>${foodItem.price}</span>
                )}
                {/* <span>After 10% off</span> */}
              </p>
            </div>
            <span style={{ color: "#27231F", width: "50px" }}>
              x{item.foodItemCount}
            </span>
            <span style={{ color: "#27231F" }}>${item.aggregatedPrice}</span>
          </div>
        );
      });
    };
    const tips = [0.1, 0.15, 0.2, "other"];
    const buildTips = () => {
      return tips.map((item, index) => {
        if (item === "other") {
          return (
            <span key="other" onClick={this.toggleModal("visibleRate")}>
              other
            </span>
          );
        } else {
          return (
            <span
              key={index}
              className={orderInfo.tipRate === item ? Styles.active : ""}
              onClick={this.changeTips(item)}
            >
              {item * 100}%
            </span>
          );
        }
      });
    };
    return (
      <View
        title="Payment"
        footer={
          <div className={Styles.confirm_footer}>
            <span onClick={this.checkOrderConfirm}>
              Checkout（$
              {plus(orderInfo.payAmount, this.state.tipsPrice)}）
            </span>
          </div>
        }
      >
        <div className={Styles.title}>{businessInfo.storeName}</div>
        {/* card list */}
        <div className={Styles.card_padding}>{buildCard()}</div>
        <div className={Styles.price}>
          {/* <Line label="You’ve saved:" content="-$11" /> */}
          <Line label="Subtotal:" content={`$${orderInfo.subTotal}`} />
        </div>
        {/* Tax */}
        <div className={Styles.price}>
          <div className={Styles.line_flex}>
            <span>
              Tax <b>x 10.1%</b>
            </span>
            <span>${round(orderInfo.tax, 2)}</span>
          </div>
        </div>
        {/* Tips */}
        <div className={Styles.price}>
          <div className={`${Styles.line_flex} ${Styles.line_flex_spe}`}>
            <span>
              Tips <b>x</b>
              <strong>{(orderInfo.tipRate || 0) * 100}%</strong>
            </span>
            <span>
              ${round(times(orderInfo.tipRate, orderInfo.payAmount), 2)}
            </span>
          </div>
          <div className={Styles.line_tags}>{buildTips()}</div>
        </div>
        {/* Price */}
        <div className={Styles.price}>
          <Line label="Order total:" content={`$${orderInfo.totalAmount}`} />
          <Line
            label="Paid:"
            content={`$${minus(
              orderInfo.totalAmount,
              orderInfo.remainingBalance
            )}`}
          />
          <Line
            label="Remaining balance:"
            content={`$${orderInfo.remainingBalance}`}
            bold
          />
        </div>
        {/* pay type */}
        <div className={Styles.price}>
          <div
            className={Styles.line_flex}
            onClick={this.toggleModal("visibleAmount")}
          >
            <span>I’d like to pay</span>
            {orderInfo.payAmount === orderInfo.remainingBalance ? (
              <span className={Styles.line_flex_auto}>
                All the remaining balance
              </span>
            ) : (
              <span className={Styles.line_flex_auto}>
                ${orderInfo.payAmount}
              </span>
            )}
            <i className={Styles.arrow_right} />
          </div>
        </div>
        <div className={Styles.payment_method}>
          <p>Payment method:</p>
          <div className={Styles.confirm_type}>
            <div
              className={this.state.useBankCard ? Styles.active : ""}
              onClick={this.bankCard(true)}
            >
              <i className={Styles.sign_icon} />
              <span>Add a bank card</span>
            </div>
            <div
              className={!this.state.useBankCard ? Styles.active : ""}
              onClick={this.bankCard(false)}
            >
              <i className={Styles.sign_icon} />
              <span>Payment offline</span>
            </div>
          </div>
          {this.state.useBankCard && (
            <div>
              <BankCard bankInfo={this.bankInfo} />
            </div>
          )}
        </div>
        {/* footer */}
        {/* 选择金额 */}
        <SelectAmount
          closeAmount={this.closeAmount}
          onOk={this.hasSelectAmount}
          remainingBalance={this.state.orderInfo.remainingBalance}
          visibleAmount={this.state.visibleAmount}
        />
        {/*  选择小费税率 */}
        <SelectRate
          close={this.closeRate}
          visibleRate={this.state.visibleRate}
          onOk={this.hasSelectRate}
        />
      </View>
    );
  }
}

export default connect(state => {
  return {
    orders: state.orders
  };
})(HocLink(Index));
