export default {
  get: (keyName, url) => {
    const urlStr = url || window.location.href;
    // eslint-disable-next-line
    const name = keyName.replace(/[\[\]]/g, "\\$&");
    const regex = new RegExp(`[?&]${name}(=([^&#]*)|&|#|$)`);
    const results = regex.exec(urlStr);

    if (!results) {
      return null;
    }
    if (!results[2]) {
      return "";
    }
    return decodeURIComponent(results[2].replace(/\+/g, " "));
  },
  replace: (paramName, replaceWith, url) => {
    const oUrl = url || window.location.href.toString();
    const re = new RegExp(`${paramName}=[^&]*`, "gi");

    let nUrl;

    if (
      replaceWith === undefined ||
      replaceWith === null ||
      replaceWith === ""
    ) {
      nUrl = oUrl.replace(re, "");
    } else {
      nUrl = oUrl.replace(re, `${paramName}=${replaceWith}`);
    }

    return nUrl.replace(/\?$/, "");
  },
  getAllParams: url => {
    const result = {};
    if (!url) {
      return result;
    }
    const urlS = new URL(url);
    const searchParams = new URLSearchParams(urlS.search);
    // eslint-disable-next-line
    for (const i of searchParams) {
      // eslint-disable-next-line
      result[i[0]] = i[1];
    }
    return result;
  }
};
