import {
  luckyDraw,
  emailBind,
  storeAndFoodItem,
  needHelp,
  emailReceipt,
  transactionsOrder
} from "../server";
export default {
  namespace: "global",
  state: {},
  effects: {
    *luckyDraw({ payload, success }, { call, put }) {
      try {
        const res = yield call(luckyDraw, {
          ...payload
        });
        return res.data;
      } catch (e) {
        console.log(e);
      }
    },
    *emailBind({ payload, success }, { call, put }) {
      try {
        const res = yield call(emailBind, {
          ...payload
        });
        return res;
      } catch (e) {
        console.log(e);
      }
    },
    *emailReceipt({ payload, success }, { call, put }) {
      try {
        const res = yield call(emailReceipt, {
          ...payload
        });
        return res;
      } catch (e) {
        console.log(e);
      }
    },
    // 提交菜品评价
    *storeAndFoodItem({ payload, success }, { call, put }) {
      try {
        yield call(storeAndFoodItem, {
          ...payload
        });
        return true;
      } catch (e) {
        console.log(e);
      }
    },
    *needHelp({ payload, success }, { call, put }) {
      try {
        let res = yield call(needHelp, {
          ...payload
        });
        return res;
      } catch (e) {
        console.log(e);
      }
    },
    *transactionsOrder({ payload, success }, { call, put }) {
      try {
        let res = yield call(transactionsOrder, {
          ...payload
        });
        if (res.code === 200) {
          return res.data;
        } else {
          return [];
        }
      } catch (e) {
        console.log(e);
      }
    }
  },
  reducers: {}
};
