import {
  getFoodItemsDetail,
  reviewsFoodItem,
  scanOrder,
  scanDishes,
  foodItemPicGet
} from "../../server";
export default {
  namespace: "orderInfo",
  state: {
    // 菜单信息
    foodItems: {},
    // 菜品评价
    reviewsFoodItemList: []
  },
  effects: {
    // 查询菜品明细
    *getFoodItemsDetail({ payload, success }, { call, put }) {
      const response = yield call(getFoodItemsDetail, {
        ...payload
      });
      yield put({
        type: "foodItemsDetail",
        payload: { ...response.data }
      });
    },
    // 获取菜品评价
    *reviewsFoodItem({ payload, success }, { call, put }) {
      const response = yield call(reviewsFoodItem, {
        ...payload
      });
      yield put({
        type: "reviewsFoodItemList",
        payload: [...response.data]
      });
    },
    *scanOrder({ payload }, { call, put }) {
      const response = yield call(scanOrder, {
        ...payload
      });
      return response.data;
    },
    *scanDishes({ payload }, { call, put }) {
      const response = yield call(scanDishes, {
        ...payload
      });
      return response.data;
    },
    *foodItemPicGet({ payload }, { call, put }) {
      const response = yield call(foodItemPicGet, {
        ...payload
      });
      return response.data;
    }
  },
  reducers: {
    // 设置菜品信息
    foodItemsDetail(state, { payload }) {
      state.foodItems = payload;
      return { ...state };
    },
    reviewsFoodItemList(state, { payload }) {
      state.reviewsFoodItemList = payload;
      return { ...state };
    }
  }
};
