import React, { Component } from "react";
import Styles from "./index.module.css";
class Index extends Component {
  state = {
    cardnumber: "",
    expMonth: "",
    expYear: ""
  };
  cardNumberChange = e => {
    this.setState({
      cardnumber: e.target.value
    });
  };
  addbankCard = () => {
    const { cardNumber, expMonth, expYear, cvs } = this.state;
    this.props.dispatch({
      type: "bankCard/addbankCard",
      payload: {
        cardNumber,
        expMonth,
        expYear,
        cvs
      }
    });
  };
  // 值变化
  inputChange = target => {
    return e => {
      if (
        (target === "expMonth" || target === "expYear") &&
        e.target.value.length > 2
      ) {
        return false;
      }
      this.setState(
        {
          [target]: e.target.value
        },
        () => {
          const { cardNumber, expMonth, expYear, cvs } = this.state;
          if (cardNumber && expMonth && expYear && cvs) {
            this.setState({
              showBtn: true
            });
          } else {
            this.setState({
              showBtn: false
            });
          }
          this.props.bankInfo({ cardNumber, expMonth, expYear, cvs });
        }
      );
    };
  };
  render() {
    return (
      <div className={Styles.page}>
        <p>Enter your card information</p>
        <div className={Styles.bankcard_section}>
          <input
            className={Styles.bankcard_line}
            name="addCreditCardNumber"
            type="number"
            onChange={this.inputChange("cardNumber")}
            placeholder="Card number"
          />
          <input
            className={Styles.bankcard_line}
            type="number"
            onChange={this.inputChange("cvs")}
            placeholder="CVV"
          />
          <span className={Styles.select_date}>Expiration date</span>
          <div className={`${Styles.select_line}`}>
            <input
              type="number"
              placeholder="MM"
              onChange={this.inputChange("expMonth")}
              value={this.state.expMonth}
              id="creditCardMonth"
              name="creditCardMonth"
            />
            <input
              type="number"
              placeholder="YY"
              id="creditCardYear"
              value={this.state.expYear}
              onChange={this.inputChange("expYear")}
              name="creditCardYear"
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Index;
