import { payments, offlinePay, printCheckout } from "../../server";
export default {
  namespace: "paymentPreview",
  state: {},
  effects: {
    // 在线支付
    *payments({ payload }, { call, put }) {
      try {
        let res = yield call(payments, {
          ...payload
        });
        return res;
      } catch (e) {
        console.log(e);
      }
    },
    // 线下支付
    *offlinePay({ payload }, { call, put }) {
      try {
        let res = yield call(offlinePay, {
          ...payload
        });
        return res;
      } catch (e) {
        console.log(e);
      }
    },
    // 下单成功 打印订单
    *printCheckout({ payload, success }, { call, put }) {
      try {
        const res = yield call(printCheckout, {
          ...payload
        });
        return res;
      } catch (e) {
        console.log(e);
      }
    }
  },
  reducers: {}
};
