import React from "react";
import full from "../images/icon_star_full@2x.png";
import half from "../images/icon_star_half@2x.png";
import empty from "../images/icon_star_null@2x.png";
import Styles from "./index.module.css";
export default function(props = {}) {
  // 封装组件，形成评分组件
  const buildStart = () => {
    let { totalReviewScore, reviewTimes } = props;
    let score = 0;
    if (props.score) {
      score = props.score;
    } else {
      if (reviewTimes) {
        score = totalReviewScore / reviewTimes;
      } else {
        score = 0;
      }
    }
    if (score > 5) {
      score = 5;
    }
    let intScore = window.parseInt(score);
    let arra = [];
    for (let i = 1; i <= intScore; i++) {
      arra.push(<img src={full} alt="" key={i} />);
    }
    if (score && score < 5) {
      let stringScore = score + "";
      let newarra = stringScore.split(".");
      if (newarra[1]) {
        arra.push(<img src={half} alt="" key={arra.length + 1} />);
      }
    }
    while (arra.length < 5) {
      arra.push(<img src={empty} alt="" key={arra.length + 1} />);
    }
    return arra;
  };
  return (
    <div
      className={`${Styles.star_flex} ${
        props.className ? props.className : ""
      }`}
    >
      {buildStart()}
    </div>
  );
}
