import React, { Component } from "react";
import Styles from "./index.module.css";
import GetStar from "../../component/getStart";
import View from "../../component/view";
import { connect } from "dva";
import Params from "../../tools/param";
// 高阶组件 统一link跳转
import HocLink from "../../component/link";
import { Toast, ImagePicker } from "antd-mobile";
import { host } from "../../server";
var jQuery = require("jquery");

class Index extends Component {
  constructor(props) {
    super(props);
    // 从url中解析出storeId
    let orderId = Params.get("orderId", window.location.href);
    this.state = {
      orderId,
      storeId: localStorage.storeId,
      // 已点菜品
      scanDishes: [],
      scoreContent: "",
      comprehensiveScore: "",
      shopImage: [],
      arrow: true
    };
  }
  componentDidMount() {
    // 商家信息
    this.getBusinessInfo(this.state.storeId);
    this.orderdishes({ orderId: this.state.orderId });
  }
  // 获取订单中的菜品
  orderdishes = params => {
    this.props
      .dispatch({
        type: "orders/orderdishes",
        payload: {
          ...params
        }
      })
      .then(data => {
        this.props.hideLoading();
        data.forEach((item, index) => {
          let key = `arrow_list_${index}`;
          this.setState({
            [key]: true
          });
        });
        this.setState({
          scanDishes: data
        });
      });
  };

  // 获取商家信息
  getBusinessInfo(storeId) {
    this.props.dispatch({
      type: "orders/getBusinessInfo",
      payload: {
        storeId
      }
    });
  }
  toggleArrow = () => {
    this.setState({
      arrow: !this.state.arrow
    });
  };
  toggleList = index => {
    return () => {
      let key = `arrow_list_${index}`;
      this.setState({
        [key]: !this.state[key]
      });
    };
  };
  // 店铺评分
  comprehensiveScore = comprehensiveScore => {
    this.setState({
      comprehensiveScore
    });
  };
  // 店铺评价
  scoreContent = e => {
    this.setState({
      scoreContent: e.target.value
    });
  };
  // 菜品评分
  foodComprehensiveScore = index => {
    return comprehensiveScore => {
      let scanDishes = [...this.state.scanDishes];
      let foodItem = scanDishes[index];
      foodItem.foodItem.comprehensiveScore = comprehensiveScore;
      // 设置菜品评分
      this.setState({
        scanDishes
      });
    };
  };
  // 菜品评价内容
  foodContent = index => {
    return e => {
      let scanDishes = [...this.state.scanDishes];
      let foodItem = scanDishes[index];
      foodItem.foodItem.scoreContent = e.target.value;
      // 设置菜品评分
      this.setState({
        scanDishes
      });
    };
  };
  // 提交评价
  confirm = () => {
    let foodItemReviews = [];
    this.state.scanDishes.forEach(item => {
      let list = item.pictureIdList || [];
      // 返回餐品pictureId
      let pictureIdList = [];
      list.forEach(k => {
        pictureIdList.push(k.pictureId);
      });
      foodItemReviews.push({
        comprehensiveScore: item.foodItem.comprehensiveScore || 0,
        foodItemId: item.foodItem.foodItemId,
        pictureIdList: pictureIdList || [],
        scoreContent: item.foodItem.scoreContent || ""
      });
    });
    let list = this.state.shopImage || [];
    // 返回店铺评价pictureId
    let shopImage = [];
    list.forEach(k => {
      shopImage.push(k.id);
    });
    let params = {
      comprehensiveScore: this.state.comprehensiveScore || 0,
      foodItemReviews: [...foodItemReviews],
      orderId: this.state.orderId,
      pictureIdList: shopImage || [],
      scoreContent: this.state.scoreContent,
      storeId: this.state.storeId
    };
    this.props
      .dispatch({
        type: "global/storeAndFoodItem",
        payload: {
          ...params
        }
      })
      .then(() => {
        Toast.info("success", 2);
        this.props.hocLink(`#/paymentResult?orderId=${this.state.orderId}`)();
      });
  };
  imagesChange = (foodItemId, index) => {
    return (files, type, index) => {
      // 如果没有index 就是添加图片的操作
      if (!index) {
        let _this = this;
        Toast.loading("loading");
        let url = `${host}/pictures/customer/upload`;
        let formData = new FormData();
        formData.append("picture", files[files.length - 1].file);
        jQuery.ajax({
          url,
          type: "post",
          data: formData,
          contentType: false,
          processData: false,
          success: function({ data = {} }) {
            let scanDishes = _this.state.scanDishes || [];
            scanDishes.forEach((item, index) => {
              item.pictureIdList = item.pictureIdList || [];
              item.pictureIdList.push({
                pictureId: data.pictureId,
                pictureUrl: data.pictureUrl
              });
            });
            _this.setState({
              scanDishes
            });
            Toast.hide();
          },
          error: function(e) {
            Toast.hide();
            if (e.responseJSON && e.responseJSON.code !== 200) {
              Toast.info("error", 1);
            }
          }
        });
      }
    };
  };
  imagesShopChange = (files, type, index) => {
    // 如果没有index 就是添加图片的操作
    if (!index) {
      let _this = this;
      Toast.loading("loading");
      let url = `${host}/pictures/customer/upload`;
      let formData = new FormData();
      formData.append("picture", files[files.length - 1].file);
      jQuery.ajax({
        url,
        type: "post",
        data: formData,
        contentType: false,
        processData: false,
        success: function({ data = {} }) {
          let shopImage = _this.state.shopImage || [];
          shopImage.push({
            id: data.pictureId,
            url: data.pictureUrl
          });
          _this.setState({
            shopImage
          });
          Toast.hide();
        },
        error: function(e) {
          Toast.hide();
          if (e.responseJSON && e.responseJSON.code !== 200) {
            Toast.info("error", 1);
          }
        }
      });
    }
  };
  render() {
    const { businessInfo = {} } = this.props.orders;
    const { scanDishes = [] } = this.state;
    const buildList = () => {
      return scanDishes.map((item, index) => {
        const { foodItem } = item;
        let primaryPictureUrl = foodItem.primaryPictureUrl || "";
        let files = [];
        let pictureIdList = item.pictureIdList || [];
        pictureIdList.forEach((item, index) => {
          files.push({
            url: item.pictureUrl,
            id: item.pictureId
          });
        });
        return (
          <div className={Styles.dishes_section} key={index}>
            {index === 0 && <p className={Styles.title}>Restaurant Overall</p>}
            <div className={Styles.res_evaluate}>
              <div className={Styles.res_evaluate_section}>
                <img src={primaryPictureUrl} alt="" />
                <span>
                  <b>{foodItem.name}</b>
                  <GetStar
                    onChange={this.foodComprehensiveScore(index)}
                    className={Styles.big_star}
                  />
                </span>
                <div
                  className={`${Styles.res_arrow} ${
                    this.state[`arrow_list_${index}`] ? Styles.show_more : ""
                  }`}
                  onClick={this.toggleList(index)}
                >
                  <i className={Styles.detail_more} />
                </div>
              </div>
            </div>
            <div
              className={`${Styles.evaluate_info} ${
                !this.state[`arrow_list_${index}`] ? "" : Styles.hide
              }`}
            >
              <textarea
                className={Styles.review_form}
                onChange={this.foodContent(index)}
                placeholder="Please enter your review"
              ></textarea>
              {/* <img src={Photo_upload} alt="" /> */}
              <ImagePicker
                files={files}
                onChange={this.imagesChange(foodItem.foodItemId, index)}
                onImageClick={(index, fs) => console.log(index, fs)}
                selectable={files.length < 4}
                accept="image/jpeg,image/jpg,image/png"
              />
            </div>
          </div>
        );
      });
    };
    let footer = (
      <div className={Styles.confirm_footer}>
        <span onClick={this.confirm}>Submit</span>
      </div>
    );
    return (
      <View
        footer={footer}
        title="Rate your experience"
        className="footer_flex"
      >
        {/* 餐厅评价 */}
        <div className={Styles.section}>
          <p className={Styles.title}>Restaurant Overall</p>
          <div className={Styles.res_evaluate}>
            <div className={Styles.res_evaluate_section}>
              <img src={businessInfo.logoPictureUrl} alt="" />
              <span>{businessInfo.storeName}</span>
              <div
                className={`${Styles.res_arrow} ${
                  this.state.arrow ? Styles.show_more : ""
                }`}
                onClick={this.toggleArrow}
              >
                <i className={Styles.detail_more} />
              </div>
            </div>
            <GetStar
              onChange={this.comprehensiveScore}
              className={Styles.big_star}
            />
          </div>
          <div
            className={`${Styles.evaluate_info} ${
              !this.state.arrow ? "" : Styles.hide
            }`}
          >
            <textarea
              className={Styles.review_form}
              onChange={this.scoreContent}
              placeholder="Please enter your review"
            ></textarea>
            <ImagePicker
              files={this.state.shopImage}
              onChange={this.imagesShopChange}
              onImageClick={(index, fs) => console.log(index, fs)}
              selectable={this.state.shopImage.length < 4}
              accept="image/jpeg,image/jpg,image/png"
            />
          </div>
        </div>
        {/* 菜品评价 */}
        {buildList()}
      </View>
    );
  }
}

export default connect(state => {
  return {
    orders: state.orders
  };
})(HocLink(Index));
