import React, { Component } from "react";
import Styles from "./index.module.css";
import OrderCalc from "../../component/orderCalc";
import Modal from "../../component/modal";
import View from "../../component/view";
import { connect } from "dva";
import { Toast } from "antd-mobile";
import { plus, round } from "../../tools/number-precision";
import OrderConfirm from "../orderConfirm";
let str =
  "The staff will bring the food to you once it's ready. Thank you for your patience.";
class Index extends Component {
  state = {
    dinnerIn: true,
    visible: false,
    modalContent: str,
    itemCancel: false,
    orderInfo: {}
  };
  componentDidMount() {
    let params = {
      storeId: this.props.storeId,
      tableNumber: this.props.tableNumber
    };
    this.getTableStatus(params).then((tableInfo = {}) => {
      if (tableInfo.state === "OCCUPIED") {
        Toast.loading("loading");
        this.scanOrder(params);
      }
    });
  }
  getTableStatus(params) {
    return this.props.dispatch({
      type: "orders/tablesStatus",
      payload: params
    });
  }
  //获取生成的订单
  scanOrder = params => {
    this.props
      .dispatch({
        type: "orderInfo/scanOrder",
        payload: {
          ...params
        }
      })
      .then(data => {
        this.setState({
          orderInfo: data
        });
        this.orderdishes({ orderId: data.orderId });
      });
  };
  // 获取订单中的菜品
  orderdishes = params => {
    this.props
      .dispatch({
        type: "orders/orderdishes",
        payload: {
          ...params
        }
      })
      .then(data => {
        Toast.hide();
        this.setState({
          orderList: data
        });
      });
  };
  //切换类型
  dinnerIn = dinnerIn => {
    return () => {
      let modalContent = "";
      if (dinnerIn === true) {
        modalContent = str;
      } else {
        modalContent = "";
      }
      this.setState({
        dinnerIn,
        modalContent
      });
    };
  };
  //监听手机号
  watchPhone = e => {
    let { value = "" } = e.target;
    if (value.length > 11) {
      return false;
    }
    if (value.length > 0) {
      this.setState({
        phoneNumber: value,
        showCloseIcon: true
      });
    } else {
      this.setState({
        showCloseIcon: false
      });
    }
  };
  //清除手机号
  clearPhone = () => {
    this.setState({
      phoneNumber: ""
    });
  };
  //显示modal
  checkOrderConfirm = () => {
    let { tableInfo } = this.props.orders;
    if (tableInfo.state === "OCCUPIED") {
      this.eatInStore();
    } else {
      this.toggleVisible();
    }
  };
  //切换modal
  toggleVisible = () => {
    this.setState({
      visible: !this.state.visible
    });
  };
  // orderNumChange
  orderChange = foodItemId => {
    return num => {
      this.props.dispatch({
        type: "orders/orderNumChange",
        payload: {
          foodItemId,
          num
        }
      });
    };
  };
  // 点菜或者加菜下单
  eatInStore = () => {
    let { foodItems, tableInfo } = this.props.orders;
    let { orderInfo } = this.state;
    let list = [];
    foodItems.forEach(item => {
      if (item.foodItem.num) {
        list.push({
          foodItemCount: item.foodItem.num,
          foodItemId: item.foodItem.foodItemId
        });
      }
    });
    let type = "orders/eatInStore";
    let payload = {
      foodItems: list,
      storeId: this.props.storeId,
      tableNumber: this.props.tableNumber
    };
    // 根据桌子状态进行不同请求
    if (tableInfo.state === "OCCUPIED") {
      type = "orders/addMoreDishes";
      payload.lastTwoDigit = orderInfo.uuid.substring(6, 8);
      if (!this.state.digitVaild) {
        this.setState({
          orderConfirm: true,
          lastTwoDigit: payload.lastTwoDigit
        });
        return false;
      }
    }
    Toast.loading("loading");
    this.props
      .dispatch({
        type,
        payload
      })
      .then(res => {
        Toast.hide();
        if (res.code === 200) {
          // 打印下单的菜品
          this.props.dispatch({
            type: "orders/printDishes",
            payload: {
              orderId: res.data.orderId
            }
          });
          setTimeout(() => {
            Toast.info("Order placed successfully", 2);
          }, 1500);
          window.location.href = `#/orderCheck?orderId=${res.data.orderId}`;
        } else {
          Toast.info(res.message, 1);
        }
      });
  };
  // 输入正确的uuid后两位 进行操作
  addMoreDish = () => {
    this.setState(
      {
        digitVaild: true
      },
      () => {
        this.eatInStore();
      }
    );
  };
  render() {
    let {
      foodItems = [],
      totalPrice,
      businessInfo,
      tableInfo = {}
    } = this.props.orders;
    let { orderList = [], orderInfo } = this.state;
    const buildCard = () => {
      return foodItems.map((item = {}, index) => {
        const { foodItem } = item;
        if (foodItem.num) {
          return (
            <div className={Styles.card} key={index}>
              <img src={`${foodItem.primaryPictureUrl}`} alt="" />
              <div className={Styles.card_info}>
                <span>{foodItem.name}</span>
                <p>
                  {foodItem.dailySpecialPrice ? (
                    <span>${foodItem.dailySpecialPrice}</span>
                  ) : (
                    <span>${foodItem.price}</span>
                  )}
                </p>
              </div>
              <OrderCalc
                value={foodItem.num}
                validZero
                confirm={this.sureConfrim}
                name={foodItem.name}
                onChange={this.orderChange(foodItem.foodItemId)}
              />
            </div>
          );
        }
        return null;
      });
    };
    const buildOrderList = () => {
      return orderList.map((item, index) => {
        const { foodItem } = item;
        let primaryPictureUrl = foodItem.primaryPictureUrl;
        return (
          <div className={`${Styles.card} ${Styles.card_spe}`} key={index}>
            <img src={primaryPictureUrl} alt="" />
            <div className={Styles.card_info}>
              <span>{foodItem.name}</span>
              <p>
                {foodItem.dailySpecialPrice ? (
                  <span>${foodItem.dailySpecialPrice}</span>
                ) : (
                  <span>${foodItem.price}</span>
                )}
              </p>
            </div>
            <span style={{ color: "#27231F", width: "50px" }}>
              x{item.foodItemCount}
            </span>
            <span style={{ color: "#27231F" }}>${item.aggregatedPrice}</span>
          </div>
        );
      });
    };
    return (
      <View
        title="Cart"
        className={`${Styles.order_preview}`}
        footer={
          <div className={Styles.confirm_footer}>
            <span onClick={this.checkOrderConfirm}>Place order</span>
            <span style={{ marginTop: "0.08rem" }} onClick={this.props.close}>
              Back to menu
            </span>
          </div>
        }
      >
        {this.state.orderConfirm && (
          <OrderConfirm
            onSure={this.addMoreDish}
            lastTwoDigit={this.state.lastTwoDigit}
          />
        )}
        <div className={Styles.title}>{businessInfo.storeName}</div>
        <p className={Styles.in_your_cart}>In your cart</p>
        {/* card list */}
        <div>{buildCard()}</div>
        {/* 如果有已经点过的菜 */}
        {tableInfo.state === "OCCUPIED" && (
          <div style={{ opacity: "0.6" }}>
            <p className={Styles.in_your_cart}>already ordered</p>
            {buildOrderList()}
          </div>
        )}
        {/* 价格 */}
        <div className={Styles.price}>
          {/* <div className={Styles.line_flex}>
            <span>You've saved:</span>
            <span>-$10</span>
          </div> */}
          <div className={Styles.line_flex}>
            <span>Subtotal:</span>
            <span>
              <b>${plus(totalPrice, orderInfo.subTotal)}</b>
            </span>
          </div>
        </div>
        {/* 选择类型  */}
        {/* <div className={Styles.confirm_type}>
          <div
            className={this.state.dinnerIn ? Styles.active : ""}
            onClick={this.dinnerIn(true)}
          >
            <i className={Styles.sign_icon} />
            <span>Dine in</span>
          </div>
          <div
            className={!this.state.dinnerIn ? Styles.active : ""}
            onClick={this.dinnerIn(false)}
          >
            <i className={Styles.sign_icon} />
            <span>Take out</span>
          </div>
        </div> */}
        <div style={{ paddingBottom: "0.24rem" }}>
          {/* dinner in */}
          {/* {this.state.dinnerIn && (
            <p className={Styles.tips}>
              Scan QR code later to locate your table.
            </p>
          )} */}
          {/* Take out */}
          {/* {!this.state.dinnerIn && (
            <div className={Styles.confirm_coustom}>
              <p>
                <span>Pick up time</span>
                <select name="" id="">
                  <option value="1">As soon as possible</option>
                  <option value="2">15 mins</option>
                  <option value="2">30 mins</option>
                </select>
                <i className={Styles.arrow_right} />
              </p>
              <p>
                <span>Cell phone</span>
                <input
                  type="number"
                  maxLength="11"
                  value={this.state.phoneNumber}
                  onChange={this.watchPhone}
                />
                {this.state.showCloseIcon ? (
                  <i className={Styles.field_close} onClick={this.clearPhone} />
                ) : (
                  <i className={Styles.field_edit} />
                )}
              </p>
            </div>
          )} */}
        </div>

        {/* check order */}
        {this.state.visible && (
          <Modal
            close={this.toggleVisible}
            seconds={5}
            title="You have placed your order"
            content={this.state.modalContent}
            btnText="Check My Order"
            onOk={this.eatInStore}
          />
        )}
      </View>
    );
  }
}

export default connect(state => {
  return {
    orders: state.orders,
    orderInfo: state.orderInfo
  };
})(Index);
