import React from "react";
import { connect } from "dva";

import Styles from "./index.module.css";
// import LogoOrder from "../../images/logo_order.png";
import About from "./about";
import Reviews from "./reviews";
// 高阶组件 统一link跳转
import HocLink from "../../component/link";
import OrderInfo from "../orderInfo";
import OrderPreview from "../orderPreview";
import { Toast } from "antd-mobile";
import Menu from "./menu";
// import classnames from "classnames";
class Index extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tab: 1,
      storeId: localStorage.storeId,
      tableNumber: localStorage.tableNumber,
      categoryIndex: 0,
      showOrderPreview: false
    };
  }
  componentWillMount() {
    this.props.showLoading();
  }
  componentDidMount() {
    let storeId = this.state.storeId;
    // this.scanOrder({
    //   storeId: this.state.storeId,
    //   tableNumber: this.state.tableNumber
    // }).then(res => {
    //   if (res && res.state === "ONGOING") {
    //     window.location.href = `#/orderCheck?orderId=${res.orderId}`;
    //   } else {
    //   }
    // });
    // 商家信息
    this.getBusinessInfo(storeId).then(() => {
      // 菜品信息
      this.getFoodItems(storeId).then(() => {
        this.props.hideLoading();
      });
    });
    this.getLabels(storeId);
    this.reviews(storeId);
  }
  //获取生成的订单
  scanOrder = params => {
    return this.props.dispatch({
      type: "orderInfo/scanOrder",
      payload: {
        ...params
      }
    });
  };
  // 获取商家信息
  getBusinessInfo(storeId) {
    return this.props.dispatch({
      type: "orders/getBusinessInfo",
      payload: {
        storeId
      }
    });
  }
  // 获取商家菜单
  getFoodItems(storeId) {
    return this.props.dispatch({
      type: "orders/getFoodItems",
      payload: {
        storeId
      }
    });
  }
  // 商家标签
  getLabels(storeId) {
    return this.props.dispatch({
      type: "orders/customerLabels",
      payload: {
        storeId
      }
    });
  }
  // 获取reviews
  reviews(storeId) {
    return this.props.dispatch({
      type: "orders/reviews",
      payload: {
        storeId
      }
    });
  }
  //   设置tab index
  setTab = tab => {
    return () => {
      this.setState({ tab });
    };
  };
  // 类目切换
  setCategoryIndex = categoryIndex => {
    return () => {
      this.setState({
        categoryIndex
      });
    };
  };

  // 显示orderInfo
  toggleShowOrderInfo = foodItemId => {
    return () => {
      this.setState({
        foodItemId,
        showOrderInfo: !this.state.showOrderInfo
      });
    };
  };

  // 显示orderPreview
  toggleOrderPreview = () => {
    this.setState({
      showOrderPreview: !this.state.showOrderPreview
    });
  };
  render() {
    const {
      businessInfo = {},
      foodItems = [],
      labelsList,
      category,
      totalNum,
      totalPrice,
      reviewsList
    } = this.props.orders;
    return (
      <div
        className={Styles.orders}
        style={{
          background: `url(
            ${businessInfo.avatarPictureUrl}
          )`
        }}
      >
        {this.state.showOrderInfo && (
          <OrderInfo
            foodItemId={this.state.foodItemId}
            close={this.toggleShowOrderInfo()}
          />
        )}
        {this.state.showOrderPreview && (
          <OrderPreview
            storeId={this.state.storeId || "109"}
            tableNumber={this.state.tableNumber || "a010"}
            close={this.toggleOrderPreview}
          />
        )}
        {/* order header */}
        <div className={Styles.header}>
          <img src={businessInfo.logoPictureUrl} alt="" />
          <div>
            <div className={Styles.title}>{businessInfo.storeName}</div>
            <div className={Styles.subtitle}>
              <span>{businessInfo.orderTimes} visits</span>
              {/* <b /> */}
              {/* <span>$20 per person</span> */}
            </div>
          </div>
        </div>
        {/* order body */}
        <div className={Styles.content}>
          {/* order body nav_bar */}
          <div className={Styles.content_nav}>
            <span
              className={this.state.tab === 1 ? Styles.active : ""}
              onClick={this.setTab(1)}
            >
              Menu
            </span>
            <span
              className={this.state.tab === 2 ? Styles.active : ""}
              onClick={this.setTab(2)}
            >
              About
            </span>
            <span
              className={this.state.tab === 3 ? Styles.active : ""}
              onClick={this.setTab(3)}
            >
              Reviews
            </span>
          </div>

          {/*  菜单*/}
          {this.state.tab === 1 && (
            <div className={Styles.content_body}>
              <Menu
                {...this.props}
                setCategoryIndex={this.setCategoryIndex}
                foodItems={foodItems}
                category={category}
                categoryIndex={this.state.categoryIndex}
                toggleShowOrderInfo={this.toggleShowOrderInfo}
              />
            </div>
          )}
          {/* aout */}
          {this.state.tab === 2 && (
            <div className={Styles.content_body}>
              <About
                {...this.props}
                businessInfo={businessInfo}
                labels={labelsList}
              />
            </div>
          )}
          {/* 评价 */}
          {this.state.tab === 3 && (
            <div className={Styles.content_body}>
              <Reviews
                businessInfo={businessInfo}
                reviews={reviewsList}
                reviewStr="comprehensiveScore"
              />
            </div>
          )}
        </div>

        {/* 底部购物车 */}
        {totalNum > 0 ? (
          <div className={Styles.footer} onClick={this.toggleOrderPreview}>
            <div className={Styles.cart}>
              <span>Cart ({totalNum})</span>
              <b />
              <span>${totalPrice}</span>
            </div>
          </div>
        ) : (
          <div
            className={Styles.footer}
            onClick={() => {
              Toast.info("Please choose your dishes first", 1);
            }}
          >
            <div className={Styles.cart}>
              <span>Cart</span>
            </div>
          </div>
        )}
      </div>
    );
  }
}
export default connect((state = {}) => {
  return {
    orders: state.orders
  };
})(HocLink(Index));
