import React, { Component } from "react";
import Styles from "./index.module.css";
import ConfirmExample from "../../images/img_receipt.png";
import classnames from "classnames";
import { Toast } from "antd-mobile";
class Index extends Component {
  constructor(props) {
    super(props);
    this.firstInput = this.firstInput.bind(this);
    this.secInput = this.secInput.bind(this);
    console.log(this.props.lastTwoDigit);
  }
  state = {
    canConfirm: false,
    firstDigit: "",
    secDigit: ""
  };
  onSure = () => {
    let lastTwoDigit = this.state.firstDigit + this.state.secDigit;
    if (this.props.lastTwoDigit === lastTwoDigit) {
      this.props.onSure();
    } else {
      Toast.info(
        "Please find the last two digits of your Order ID on your order ticket the staff has stuck on your table.",
        3
      );
    }
  };
  firstInput() {
    this.firstTextInput.focus();
  }
  secInput() {
    this.secTextInput.focus();
  }
  firstInputChange = e => {
    let value = e.target.value;
    if (value && value.length >= 1) {
      this.setState({
        firstDigit: value.substr(value.length - 1, value.length)
      });
      this.secInput();
    } else {
      this.setState({
        firstDigit: ""
      });
    }
  };
  secInputChange = e => {
    let value = e.target.value;
    if (value && value.length >= 1) {
      this.setState({
        secDigit: value.substr(value.length - 1, value.length)
      });
      if (!this.state.firstDigit) {
        this.firstInput();
      }
    } else {
      this.setState({
        secDigit: ""
      });
    }
  };
  render() {
    const cx = classnames.bind(Styles);
    const footerclass = cx(Styles.confirm_footer, {
      can_confirm: this.state.canConfirm
    });
    return (
      <div className={`${Styles.view} ${Styles.confirm_view}`}>
        {/* title */}
        <div className={Styles.title}>Confirm your table</div>
        <div className={Styles.view_body}>
          <p>
            Please find the last two digits of your Order ID on your order
            ticket the staff has stuck on your table.
          </p>
          <div className={Styles.enter_table}>
            <input
              type="text"
              onChange={this.firstInputChange}
              value={this.state.firstDigit}
              ref={input => {
                this.firstTextInput = input;
              }}
            />
            <input
              type="text"
              value={this.state.secDigit}
              onChange={this.secInputChange}
              ref={input => {
                this.secTextInput = input;
              }}
            />
          </div>
          <div className={Styles.confirm_example}>
            <span>Order ticket example:</span>
            <img src={ConfirmExample} alt="" />
          </div>
        </div>
        <div className={footerclass}>
          <span onClick={this.onSure}>Confirm</span>
        </div>
      </div>
    );
  }
}

export default Index;
