import React, { Component } from "react";
import Styles from "./index.module.css";
import Modal from "../../component/modal";
class Index extends Component {
  state = {
    active: 1
  };
  componentDidMount() {}
  customerCoupons = () => {
    this.props.dispatch({
      type: "paymentPreview/customerCoupons"
    });
  };
  selectCoupon = active => {
    return () => {
      this.setState({
        active
      });
    };
  };
  render() {
    const buildCard = () => {
      let arra = [];
      for (let i = 0; i <= 2; i++) {
        arra.push(
          <div
            onClick={this.selectCoupon(i)}
            className={`${Styles.section} ${this.state.active === i &&
              Styles.active} ${i === 2 ? Styles.expire : ""}`}
            key={i}
          >
            <div className={Styles.section_left}>
              <span>$29</span>
              <span>off</span>
            </div>
            <div className={Styles.card_line} />
            <div className={Styles.section_middle}>
              <span>Expire:</span>
              <span>08/22/2019</span>
            </div>
            <i className={Styles.sign_icon} />
          </div>
        );
      }
      return arra;
    };
    return (
      <Modal className={Styles.card_gift} title="Gift Card" hideBtn>
        <p className={Styles.card_gift_title}>
          Only one gift card can be applied at a time.
        </p>
        <div className={Styles.card_gift_section}>{buildCard()}</div>
      </Modal>
    );
  }
}

export default Index;
