import React, { Component } from "react";
import full from "../images/icon_star_full@2x.png";
import empty from "../images/icon_star_null@2x.png";
import Styles from "./index.module.css";

export default class Index extends Component {
  state = {};
  setSelect = i => {
    return () => {
      this.setState({
        imgIndex: i
      });
      this.props.onChange(i + 1);
    };
  };
  render() {
    // 封装组件，形成评分组件
    const buildStart = () => {
      let arra = [];
      for (let i = 0; i <= 4; i++) {
        let imgSrc = empty;
        let { imgIndex } = this.state;
        if (imgIndex >= i) {
          imgSrc = full;
        }
        arra.push(
          <img src={imgSrc} alt="" key={i} onClick={this.setSelect(i)} />
        );
      }
      return arra;
    };
    return (
      <div
        className={`${Styles.star_flex} ${
          this.props.className ? this.props.className : ""
        }`}
      >
        {buildStart()}
      </div>
    );
  }
}
