import React, { Component } from "react";
import Styles from "./index.module.css";

// 数量计算器
import OrderCalc from "../../component/orderCalc";

// 评分组件
import Star from "../../component/star";
class componentName extends Component {
  state = {};
  // orderNumChange
  orderChange = foodItemId => {
    return num => {
      this.props.dispatch({
        type: "orders/orderNumChange",
        payload: {
          foodItemId,
          num
        }
      });
    };
  };
  render() {
    const { foodItems = [], category, categoryIndex } = this.props;
    // 菜单列表
    const buildCard = () => {
      let arra = [];
      let categoryText = category[categoryIndex];
      foodItems.forEach((item, index) => {
        let foodItem = item.foodItem;
        if (item.foodItem.category === categoryText) {
          if (foodItem.primaryPictureUrl.indexOf("http") === -1) {
            foodItem.primaryPictureUrl = `http://${foodItem.primaryPictureUrl}`;
          }
          arra.push(
            <div className={Styles.content_card} key={index}>
              <div className={Styles.content_card_title}>
                <span>{foodItem.name}</span>
              </div>
              <div className={Styles.content_card_body}>
                <img
                  src={foodItem.primaryPictureUrl}
                  alt=""
                  onClick={this.props.toggleShowOrderInfo(foodItem.foodItemId)}
                />
                <div className={Styles.content_card_intro}>
                  <div className={Styles.content_card_section}>
                    <span className={Styles.content_card_amount}>
                      $
                      {foodItem.dailySpecialPrice
                        ? foodItem.dailySpecialPrice
                        : foodItem.price}
                    </span>
                    {!!foodItem.dailySpecialPrice && (
                      <span className={Styles.content_card_originPrice}>
                        ${foodItem.price}
                      </span>
                    )}
                  </div>
                  <div className={Styles.detail_card_star}>
                    <Star
                      totalReviewScore={foodItem.totalReviewScore}
                      reviewTimes={foodItem.reviewTimes}
                    />
                  </div>
                  <div className={Styles.content_sold}>
                    <span>{item.orderTimes} sold</span>
                    <OrderCalc
                      value={foodItem.num}
                      onChange={this.orderChange(foodItem.foodItemId)}
                    />
                  </div>
                </div>
              </div>
            </div>
          );
        }
      });
      return arra;
    };
    // 类目
    const buildMenu = () => {
      let arra = [];
      function titleCase(str) {
        var array = str.toLowerCase().split(" ");
        for (var i = 0; i < array.length; i++) {
          array[i] =
            array[i][0].toUpperCase() + array[i].substring(1, array[i].length);
        }
        var string = array.join(" ");

        return string;
      }

      category.forEach((item, index) => {
        let classname = "";
        // 菜单类目
        const { categoryIndex } = this.props;
        if (categoryIndex === index) {
          classname = Styles.active;
        } else if (categoryIndex - 1 === index) {
          classname = Styles.pre_active;
        } else if (categoryIndex + 1 === index) {
          classname = Styles.after_active;
        }
        arra.push(
          <span
            key={index}
            className={classname}
            onClick={this.props.setCategoryIndex(index)}
          >
            {titleCase(item)}
          </span>
        );
      });
      return arra;
    };
    return (
      <div className={Styles.menu_section}>
        {/* menu */}
        <div className={Styles.content_menu}>{buildMenu()}</div>
        {/* list build-card-list */}
        <div className={Styles.content_list}>{buildCard()}</div>
      </div>
    );
  }
}

export default componentName;
