import React, { Component } from "react";
import Modal from "./modal";
import Styles from "./index.module.css";
class Index extends Component {
  state = {
    itemCancel: false
  };
  // 关闭modal
  itemCancel = () => {
    this.setState({
      itemCancel: !this.state.itemCancel
    });
  };
  // 确定删除
  sureConfrim = () => {
    this.props.onChange(this.props.value - 1);
  };
  plus = () => {
    let value = this.props.value || 0;
    this.props.onChange(value + 1);
  };
  min = () => {
    let nextValue = this.props.value - 1;
    if (this.props.validZero && nextValue === 0) {
      this.setState({
        name: this.props.name
      });
      this.itemCancel();
    } else {
      this.props.onChange(this.props.value - 1);
    }
  };
  render() {
    return (
      <div className={Styles.content_calc}>
        {this.props.value ? (
          <span className={Styles.content_min} onClick={this.min} />
        ) : null}
        {this.props.value ? (
          <span className={Styles.content_num}>{this.props.value}</span>
        ) : null}
        <span className={Styles.content_plus} onClick={this.plus} />
        {this.state.itemCancel && (
          <Modal
            btnText="Confirm"
            title="Remove this item"
            content={this.state.name}
            hideBtn
            hideSeconds
            close={this.itemCancel}
          >
            <div className={Styles.btn_list}>
              <span onClick={this.itemCancel}>Cancel</span>
              <span onClick={this.sureConfrim}>Confirm</span>
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

export default Index;
