import {
  getBusinessInfo,
  getFoodItems,
  customerLabels,
  reviews,
  tablesStatus,
  addMoreDishes,
  eatInStore,
  regularHours,
  orderDetail,
  orderdishes,
  printDishes
} from "../../server";
import { times, plus } from "../../tools/number-precision";
export default {
  namespace: "orders",
  state: {
    // 商家信息
    businessInfo: {},
    // 菜单类目
    category: [],
    // 菜单信息
    foodItems: [],
    // 获取标签
    labelsList: []
  },
  effects: {
    // 设置商家信息
    *getBusinessInfo({ payload, success }, { call, put }) {
      const response = yield call(getBusinessInfo, {
        ...payload
      });
      yield put({
        type: "businessInfo",
        payload: { ...response.data }
      });
    },
    // 设置菜品信息
    *getFoodItems({ payload, success }, { call, put }) {
      const response = yield call(getFoodItems, {
        ...payload
      });
      yield put({
        type: "foodItemsList",
        payload: [...response.data]
      });
    },
    *regularHours({ payload, success }, { call, put }) {
      const response = yield call(regularHours, {
        ...payload
      });
      return response.data;
    },
    // 商家标签
    *customerLabels({ payload, success }, { call, put }) {
      const response = yield call(customerLabels, {
        ...payload
      });
      yield put({
        type: "labelsList",
        payload: [...response.data]
      });
    },
    *reviews({ payload, success }, { call, put }) {
      try {
        const response = yield call(reviews, {
          ...payload
        });
        yield put({
          type: "reviewsList",
          payload: [...response.data]
        });
      } catch (e) {
        console.log(e);
      }
    },
    *tablesStatus({ payload, success }, { call, put }) {
      const response = yield call(tablesStatus, {
        ...payload
      });
      yield put({
        type: "tableInfo",
        payload: { ...response.data }
      });
      return response.data;
    },
    // 加菜
    *addMoreDishes({ payload, success }, { call, put }) {
      try {
        const response = yield call(addMoreDishes, {
          ...payload
        });
        return response;
      } catch (e) {
        console.log(e);
      }
    },
    // 下单
    *eatInStore({ payload }, { call, put }) {
      try {
        const response = yield call(eatInStore, {
          ...payload
        });
        return response;
      } catch (e) {
        console.log(e);
      }
    },
    //  获取订单详情
    *orderDetail({ payload, success }, { call, put }) {
      try {
        const res = yield call(orderDetail, {
          ...payload
        });
        return res.data;
      } catch (e) {
        console.log(e);
      }
    },
    // 订单中的菜品
    *orderdishes({ payload, success }, { call, put }) {
      try {
        const res = yield call(orderdishes, {
          ...payload
        });
        return res.data;
      } catch (e) {
        console.log(e);
      }
    },
    // 下单成功 打印订单
    *printDishes({ payload, success }, { call, put }) {
      try {
        const res = yield call(printDishes, {
          ...payload
        });
        return res;
      } catch (e) {
        console.log(e);
      }
    }
  },
  reducers: {
    // 设置商家信息
    businessInfo(state, { payload }) {
      state.businessInfo = payload;
      return { ...state };
    },
    // 设置菜品信息
    foodItemsList(state, { payload = [] }) {
      let category = [];
      payload.forEach(item => {
        let k = item.foodItem.category;
        if (item.foodItem && k && category.indexOf(k) === -1) {
          category.push(k);
        }
      });
      state.category = category;
      state.foodItems = payload;
      return { ...state };
    },
    // 菜品加减
    orderNumChange(state, { payload }) {
      let totalNum = 0;
      let totalPrice = 0;
      state.foodItems.forEach(item => {
        if (item.foodItem.foodItemId === payload.foodItemId) {
          item.foodItem.num = payload.num;
        }
        let num = item.foodItem.num || 0;
        totalNum = plus(totalNum, num);
        let targetPrice =
          item.foodItem.dailySpecialPrice || item.foodItem.price || 0;
        totalPrice = plus(totalPrice, times(num, targetPrice || 0));
      });
      return { ...state, totalNum, totalPrice };
    },
    // 商家标签
    labelsList(state, { payload }) {
      state.labelsList = payload;
      return { ...state };
    },
    // 评价列表
    reviewsList(state, { payload }) {
      state.reviewsList = payload;
      return { ...state };
    },
    // 菜品明细
    getFoodItemsDetail(state, { payload }) {
      state.foodItems.forEach(item => {
        if (Number(item.foodItem.foodItemId) === Number(payload.foodItemId)) {
          state.getFoodItemsDetail = item;
        }
      });
      return { ...state };
    },
    // 桌子状态
    tableInfo(state, { payload }) {
      state.tableInfo = payload;
      return { ...state };
    }
  }
};
