import React, { Component } from "react";
import Styles from "./index.module.css";
import Modal from "../../component/modal";
import { round } from "../../tools/number-precision";
import { Toast } from "antd-mobile";
class Index extends Component {
  state = {
    selectAmount: 0,
    amount: ""
  };
  // 选择金额方式
  selectAmount = selectAmount => {
    return () => {
      this.setState({
        selectAmount
      });
    };
  };
  //监听输入金额
  watchNumer = e => {
    let { value = "" } = e.target;
    if (value.length > 0) {
      this.setState({
        amount: value,
        showCloseIcon: true
      });
    } else {
      this.setState({
        amount: value,
        showCloseIcon: false
      });
    }
  };
  //清除金额
  clearPhone = () => {
    this.setState({
      amount: "",
      showCloseIcon: false
    });
  };
  close = () => {
    if (this.props.visibleRate) {
      this.props.close();
    }
  };
  onOk = amount => {
    return () => {
      let reg = /^[0-9]+.?[0-9]*$/;
      if (!reg.test(amount)) {
        Toast.info("Please enter number", 1);
      } else {
        this.props.onOk(amount);
      }
    };
  };
  render() {
    return (
      <div>
        {/* 选择支付金额 */}
        {this.props.visibleRate && (
          <Modal hideBtn close={this.close} className={Styles.modal}>
            <div className={Styles.payment_modal}>
              <div className={Styles.email} style={{ margin: "0" }}>
                <input
                  type="text"
                  value={this.state.amount}
                  onChange={this.watchNumer}
                  placeholder="5%"
                />
                {this.state.showCloseIcon ? (
                  <i className={Styles.field_close} onClick={this.clearPhone} />
                ) : (
                  <i className={Styles.field_edit} />
                )}
                {this.state.amount || this.state.amount === 0 ? (
                  <span
                    className={Styles.active}
                    onClick={this.onOk(this.state.amount)}
                  >
                    Confirm
                  </span>
                ) : (
                  <span>Confirm</span>
                )}
              </div>
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

export default Index;
