import dva from "dva";
import "./index.css";
import Router from "./router";
import modal from "./model";
import Vconsole from "vconsole";
new Vconsole();
const app = dva();
console.log("9ac8044bae2fd103e2efc682b92b88290cb23310");
// 在dev环境下使用redux-logger，并且默认折叠起来
if (process.env.NODE_ENV === "development") {
  const { createLogger } = require("redux-logger");
  const logger = createLogger({ collapsed: true });
  app.use({ onAction: logger });
}

//  添加多modal(reducers)
modal.forEach(m => {
  app.model(m);
});

// 初始化路由
app.router(Router);

// 启动App
app.start("#root");
