import React, { Component } from "react";
import { Route } from "dva/router";
import Styles from "./index.module.css";
import "../index.css";
import Orders from "./orders";
import OrderInfo from "./orderInfo";
import OrderConfirm from "./orderConfirm";
import OrderPreview from "./orderPreview";
import OrderCheck from "./orderCheck";
import CardGift from "./cardGift";
import PaymentResult from "./paymentResult";
import PaymentPreview from "./paymentPreview";
import Evaluate from "./evaluate";
import BankCard from "./bankCard";
import { connect } from "dva";
import params from "../tools/param";

class Index extends Component {
  componentWillMount() {
    let tableNumber = params.get("tableNumber", window.location.href);
    let storeId = params.get("storeId", window.location.href);
    if (tableNumber) {
      // 桌号
      localStorage.tableNumber = tableNumber;
    }
    if (storeId) {
      // 商家ID
      localStorage.storeId = storeId;
    }
  }
  render() {
    return (
      <div className={Styles.home}>
        <div className={Styles.home_body}>
          <Route component={Orders} exact path="/" />
          {/* 点餐页 */}
          <Route component={Orders} exact path="/orders" />
          {/* 菜单详情 */}
          <Route component={OrderInfo} exact path="/orderInfo" />
          {/* 订单预览 */}
          <Route component={OrderPreview} exact path="/orderPreview" />
          {/* 订单查看 */}
          <Route component={OrderCheck} exact path="/orderCheck" />
          {/* 订单确认 */}
          <Route component={OrderConfirm} exact path="/orderConfirm" />
          {/* 优惠券 */}
          <Route component={CardGift} exact path="/cardGift" />
          {/* 支付预览 */}
          <Route component={PaymentPreview} exact path="/paymentPreview" />
          {/* 支付结果 */}
          <Route component={PaymentResult} exact path="/paymentResult" />
          {/* 评价 */}
          <Route component={Evaluate} exact path="/evaluate" />
          {/* 添加银行卡 */}
          <Route component={BankCard} exact path="/bankCard" />
        </div>
        {/* <div className={Styles.home_footer}>
          <ul className={Styles.home_ul}>
            <li className={Styles.active}>
              <i className={classNames("icon-default", "icon_home")} />
              <span>Home</span>
            </li>
            <li>
              <i className={classNames("icon-default", "icon_orders")} />
              <span>Orders</span>
            </li>
            <li>
              <i className={classNames("icon-default", "icon_me")} />
              <span>Me</span>
            </li>
          </ul>
        </div> */}
      </div>
    );
  }
}
export default connect()(Index);
