import React from "react";
import Styles from "./index.module.css";
import TakeOut from "../../images/icon_shop_takout@2x.png";
import { Toast } from "antd-mobile";
export default class Index extends React.Component {
  state = {
    hoursList: []
  };
  componentDidMount() {
    Toast.loading("loading");
    this.getHours().then(hoursList => {
      Toast.hide();
      this.setState({
        hoursList
      });
    });
  }
  // 获取营业时间
  getHours = () => {
    return this.props.dispatch({
      type: "orders/regularHours",
      payload: {
        storeId: localStorage.storeId
      }
    });
  };
  render() {
    const { businessInfo, labels = [] } = this.props;
    const buildTags = () => {
      return labels.map((item, index) => {
        return (
          <span key={index}>
            <b>{item.labelName}</b>
          </span>
        );
      });
    };
    const getDayStr = (emu = 0) => {
      let arra = ["Sun", "Mon", "Tues", "Wed", "Thurs", "Fri", "Sat"];
      return arra[emu];
    };
    const buildHours = () => {
      let arra = [];
      this.state.hoursList.forEach((item, index) => {
        arra.push(
          <div key={index} className={Styles.time_line}>
            <span>{getDayStr(item.dayOfWeek)}</span>
            <p className={Styles.time_hours}>
              <span>{item.startTime}</span>
              <span>-</span>
              <span>{item.endTime}</span>
            </p>
            {item.status !== "OPEN" && (
              <span style={{ color: "red", fontWeight: "bold" }}>
                Close now
              </span>
            )}
          </div>
        );
      });
      return arra;
    };
    return (
      <div className={Styles.about}>
        <div className={Styles.takeout}>
          <img src={TakeOut} alt="" />
          <div className={Styles.tags}>{buildTags()}</div>
        </div>
        <div className={Styles.about_info}>
          <div className={Styles.about_line}>
            <p>Hours:</p>
            <div>{buildHours()}</div>
          </div>
          <div className={Styles.about_line}>
            <p>Address:</p>
            <div>
              {`${businessInfo.businessAddress} ${businessInfo.city} ${businessInfo.state} ${businessInfo.zipcode}`}
            </div>
          </div>
          <div className={Styles.about_line}>
            <p>phone:</p>
            <div>
              <p>
                <a href={`tel:${businessInfo.phoneNumber}`}>
                  {businessInfo.phoneNumber}
                </a>
              </p>
            </div>
          </div>
        </div>
        <p className={Styles.intro}>{businessInfo.introduction}</p>
      </div>
    );
  }
}
