import React, { Component } from "react";
import Styles from "./index.module.css";
import Line from "../../component/line";
import Help from "../../images/sumary_NeedHelp@2x.png";
import View from "../../component/view";
import Params from "../../tools/param";
import { connect } from "dva";
import moment from "moment-timezone";
import { Toast } from "antd-mobile";
// 高阶组件 统一link跳转
import HocLink from "../../component/link";
class Index extends Component {
  constructor(props) {
    super(props);
    // 从url中解析出storeId
    let orderId = Params.get("orderId", window.location.href);
    this.state = {
      dinnerIn: true,
      visible: false,
      phoneNumber: "",
      orderId,
      tableNumber: localStorage.tableNumber,
      storeId: localStorage.storeId,
      // 已点菜品
      scanDishes: [],
      // 订单信息
      orderInfo: {},
      email: ""
    };
  }
  componentWillMount() {
    this.props.showLoading();
    // 商家信息
    this.getBusinessInfo(this.state.storeId);
    // this.scanOrder({
    //   tableNumber: this.state.tableNumber,
    //   storeId: this.state.storeId
    // });
    // this.scanDishes({
    //   tableNumber: this.state.tableNumber,
    //   storeId: this.state.storeId
    // });
    // 获取支付记录
    this.transactionsOrder({ orderId: this.state.orderId });
    // 获取订单详情
    this.orderDetail({
      orderId: this.state.orderId
    });
    // 获取订单的菜品信息
    this.orderdishes({
      orderId: this.state.orderId
    });
  }
  // 订单支付记录
  transactionsOrder = params => {
    this.props
      .dispatch({
        type: "global/transactionsOrder",
        payload: {
          ...params
        }
      })
      .then((paymentList = []) => {
        this.setState({
          paymentList
        });
      });
  };
  //获取付款后的订单
  orderDetail = params => {
    this.props
      .dispatch({
        type: "orders/orderDetail",
        payload: {
          ...params
        }
      })
      .then(data => {
        this.props.hideLoading();
        this.setState({
          orderInfo: data,
          uuid: data.uuid && data.uuid.substring(4, 6)
        });
      });
  };
  // 获取订单中的菜品
  orderdishes = params => {
    this.props
      .dispatch({
        type: "orders/orderdishes",
        payload: {
          ...params
        }
      })
      .then(data => {
        this.props.hideLoading();
        // Toast.info("Order placed successfully.", 2);
        this.setState({
          scanDishes: data
        });
      });
  };

  // 获取商家信息
  getBusinessInfo(storeId) {
    this.props.dispatch({
      type: "orders/getBusinessInfo",
      payload: {
        storeId
      }
    });
  }
  //获取生成的订单
  scanOrder = params => {
    this.props
      .dispatch({
        type: "orderInfo/scanOrder",
        payload: {
          ...params
        }
      })
      .then(data => {
        this.setState({
          orderInfo: data
        });
      });
  };
  scanDishes = params => {
    this.props
      .dispatch({
        type: "orderInfo/scanDishes",
        payload: {
          ...params
        }
      })
      .then(data => {
        this.props.hideLoading();
        this.setState({
          scanDishes: data
        });
      });
  };
  //监听email
  watchEmail = e => {
    let { value = "" } = e.target;
    let emailRex = /\w+@[a-z0-9]+\.[a-z]{2,4}/;
    if (emailRex.test(value)) {
      this.setState({
        canSubmit: true
      });
    } else {
      this.setState({
        canSubmit: false
      });
    }
    if (value.length > 0) {
      this.setState({
        email: value,
        showCloseIcon: true
      });
    } else {
      this.setState({
        showCloseIcon: false,
        email: ""
      });
    }
  };
  //清除email
  clearPhone = () => {
    this.setState({
      email: "",
      canSubmit: false,
      showCloseIcon: false
    });
  };
  // 发送小票
  onOk = () => {
    this.props.showLoading();
    this.props
      .dispatch({
        type: "global/emailReceipt",
        payload: {
          orderId: this.state.orderId,
          email: this.state.email
        }
      })
      .then(res => {
        this.props.hideLoading();
        if (res.code === 200) {
          Toast.info("Sent", 2);
        } else {
          if (res.message) {
            Toast.info(res.message);
          }
        }
      });
  };
  // 需要帮助
  needHelp = () => {
    Toast.loading("loading");
    this.props
      .dispatch({
        type: "global/needHelp",
        payload: {
          orderId: this.state.orderId
        }
      })
      .then(res => {
        Toast.hide();
        if (res.code === 200) {
          Toast.info("The staff has been notified. Please wait.", 2);
        } else {
          Toast.info(res.message || "error", 1);
        }
      });
  };
  render() {
    const { businessInfo = {} } = this.props.orders;
    const { scanDishes = [], orderInfo = {} } = this.state;
    const buildCard = () => {
      return scanDishes.map((item, index) => {
        let { foodItem = {} } = item;
        let primaryPictureUrl = foodItem.primaryPictureUrl;
        return (
          <div className={`${Styles.card} ${Styles.card_spe}`} key={index}>
            <img src={`${primaryPictureUrl}`} alt="" />
            <div className={Styles.card_info}>
              <span>{foodItem.name}</span>
              <p>
                {foodItem.dailySpecialPrice ? (
                  <span>${foodItem.dailySpecialPrice}</span>
                ) : (
                  <span>${foodItem.price}</span>
                )}
              </p>
            </div>
            <span style={{ color: "#27231F" }}>x{item.foodItemCount}</span>
          </div>
        );
      });
    };
    let { paymentList = [] } = this.state;
    const buildPayment = () => {
      let targetList = paymentList;
      if (!this.state.showMore) {
        targetList = [paymentList[0]];
      }
      return targetList.map((item, index) => {
        return (
          <div className={Styles.payment_list}>
            <Line label="Payment total:" content={`$${item.payAmount}`} />
            <Line label="Transaction ID:" content={item.transactionId} />
            <Line
              label="Payment time:"
              content={moment(item.createdAt)
                .utcOffset(1)
                .format("HH:mm:ss DD-MM-YYYY")}
            />
          </div>
        );
      });
    };
    return (
      <View
        title="Order Summary"
        footer={
          <div>
            {orderInfo.state === "ONGOING" && (
              <div className={Styles.btn_list}>
                <span onClick={this.props.hocLink(`#/orders`)}>Order more</span>
                <span
                  className={Styles.active}
                  onClick={this.props.hocLink(
                    `#/paymentPreview?orderId=${this.state.orderId}`
                  )}
                >
                  Payment
                </span>
              </div>
            )}
            {/* 待评价 */}
            {orderInfo.state === "TOREVIEW" && (
              <div className={Styles.confirm_footer}>
                <span
                  onClick={this.props.hocLink(
                    `#/evaluate?orderId=${this.state.orderId}`
                  )}
                >
                  Rate Now
                </span>
              </div>
            )}
          </div>
        }
      >
        <div style={{ paddingBottom: "0.24rem" }}>
          <div className={Styles.title}>{businessInfo.storeName}</div>
          <p className={Styles.cart_line}>Your order has been placed</p>
          {/* card list */}
          <div>{buildCard()}</div>
          {/* 未支付状态 */}
          {orderInfo.state === "ONGOING" && (
            <div>
              <div className={Styles.price}>
                <Line label="Order ID:" content={`${orderInfo.orderId}`} />
                <Line
                  label="Order time:"
                  content={moment(orderInfo.lastUpdatedAt)
                    .utcOffset(1)
                    .format("HH:mm:ss DD-MM-YYYY")}
                />
              </div>
              {/* 待支付 */}
              {orderInfo.state === "ONGOING" && (
                <div>
                  {!orderInfo.paidAmount ? (
                    <div className={Styles.price}>
                      <Line
                        label="Subtotal:"
                        content={`$${orderInfo.subTotal}`}
                        bold
                      />
                    </div>
                  ) : (
                    <div className={Styles.price}>
                      {/* 待部分支付 */}
                      <Line
                        label="Subtotal:"
                        content={`$${orderInfo.subTotal}`}
                      />
                      <Line
                        label="Paid:"
                        content={`$${orderInfo.paidAmount}`}
                      />
                      <Line
                        label="Remaining balance:"
                        content={`$${orderInfo.remainingBalance}`}
                        bold
                      />
                    </div>
                  )}
                  {/* need help */}
                  <img
                    className={Styles.order_help}
                    src={Help}
                    alt=""
                    onClick={this.needHelp}
                  />
                </div>
              )}
              {/* 2:未支付外带 */}
              {/* {orderStatus === 2 && (
                <div className={Styles.price}>
                  <Line
                    label="Pick up time:"
                    content={moment().tz("America/Los_Angeles").format("HH:mm:ss DD-MM-YYYY")}
                  />
                  <Line label="Cell phone:" content={orderInfo.phoneNumber} />
                </div>
              )} */}
            </div>
          )}
          {/* 已支付状态 */}
          {(orderInfo.state === "TOREVIEW" ||
            orderInfo.state === "COMPLETED") && (
            <div className={Styles.price}>
              <Line label="Subtotal:" content={`$${orderInfo.subTotal}`} />
              <Line label="Tax:" content={`$${orderInfo.tax}`} />
              <Line label="Tips:" content={`$${orderInfo.tip}`} />
              <Line label="Total:" content={`$${orderInfo.totalAmount}`} bold />
            </div>
          )}
          {/* 支付完成 or 待评价 */}
          {(orderInfo.state === "TOREVIEW" ||
            orderInfo.state === "COMPLETED") &&
            (paymentList.length > 1 && (
              <div className={Styles.price}>
                <div>{buildPayment()}</div>
                <div
                  className={`${this.state.showMore && Styles.show_more} ${
                    Styles.under_handle
                  }`}
                  onClick={() => {
                    this.setState({ showMore: !this.state.showMore });
                  }}
                >
                  <span>More Payments</span>
                  <i className={Styles.detail_more} />
                </div>
              </div>
            ))}
          {/* 支付完成 or 待评价 */}
          {(orderInfo.state === "TOREVIEW" ||
            orderInfo.state === "COMPLETED") && (
            <div className={Styles.enter}>
              <p>Send receipt to email</p>
              <div className={Styles.email}>
                <input
                  type="text"
                  value={this.state.email}
                  onChange={this.watchEmail}
                  placeholder="Your email address"
                />
                {this.state.showCloseIcon ? (
                  <i className={Styles.field_close} onClick={this.clearPhone} />
                ) : (
                  <i className={Styles.field_edit} />
                )}
                {this.state.canSubmit ? (
                  <span className={Styles.active} onClick={this.onOk}>
                    Confirm
                  </span>
                ) : (
                  <span>Confirm</span>
                )}
              </div>
            </div>
          )}
        </div>
      </View>
    );
  }
}

export default connect(state => {
  return {
    orders: state.orders
  };
})(HocLink(Index));
