import React from "react";
import Styles from "./index.module.css";
import Star from "../../component/star";
import Reviews from "../../component/reviews";
import { strip } from "../../tools/number-precision";
export default class Index extends React.Component {
  render() {
    const { businessInfo, reviews = [] } = this.props;
    return (
      <div className={Styles.reviews}>
        <div className={Styles.reviews_title}>
          <span>
            {strip(businessInfo.totalReviewScore / businessInfo.reviewTimes, 2)}
          </span>
          <Star
            score={strip(
              businessInfo.totalReviewScore / businessInfo.reviewTimes,
              2
            )}
          />
          <span>{businessInfo.reviewTimes} Ratings</span>
        </div>
        <Reviews
          reviews={reviews}
          reviewStr={this.props.reviewStr}
          bannerPirture
        />
      </div>
    );
  }
}
