import request from "./tools/fetch";
export let host = "https://api.tableflash.com/v1";

// 获取商家信息
export async function getBusinessInfo(params) {
  return request(pingSrc(`stores/${params.storeId}`), {
    method: "GET"
  });
}
// 获取商家菜单
export async function getFoodItems(params) {
  return request(pingSrc(`foodItems`), {
    method: "GET",
    body: {
      ...params
    }
  });
}
