import fetch from "dva/fetch";
import qs from "qs";
function parseJSON(response) {
  return response.json();
}

function checkStatus(response) {
  if (response.status >= 200 && response.status < 300) {
    return response;
  }
  return response;
}

/**
 * Requests a URL, returning a promise.
 *
 * @param  {string} url       The URL we want to request
 * @param  {object} [options] The options w want to pass to "fetch"
 * @return {object}           An object containing either "data" or "err"
 */
export default function request(url, options = {}) {
  options.body = options.body ? options.body : {};
  options.headers = {
    "Content-type": "application/json",
    Authorization: localStorage.Authorization
  };
  for (let name in options.body) {
    if (options.body[name] === undefined) {
      delete options.body[name];
    }
  }
  if (options.method === "POST" || options.method === "PUT") {
    options.body = JSON.stringify(options.body);
  }
  if (options.method === "POSTFORM") {
    options.headers = {
      "Content-type": "application/x-www-form-urlencoded",
      Authorization: `Basic ${window.btoa("celesea-app:celesea")}`
    };
    options.method = "POST";
    options.body = qs.stringify(options.body);
  }
  // safari浏览器 get传body参数会报错，所以在这里检查一次
  if (options.method === "GET" && options.body) {
    options.body = {
      ...options.body
    };
    url = `${url}?${qs.stringify(options.body)}`;
    delete options.body;
  }
  return fetch(url, options)
    .then(checkStatus)
    .then(parseJSON)
    .then(data => {
      return data;
    })
    .catch(err => ({ err }));
}
