import React from 'react';
import Index from './routers/index';

import { Route, Switch, Router } from 'dva/router';

// 顶层父组件
function RouteConfig({ history }) {
  return (
    <Router history={history}>
        <Switch>
          <Route component={Index}
              exact
              path="/"
          />
          <Index />
        </Switch>
      </Router>
  );
}

export default RouteConfig;
