import React, { Component } from "react";
import Styles from "./index.module.css";
import Modal from "../../component/modal";
import luckyCard from "../../component/lucky";
import Line from "../../component/line";
import View from "../../component/view";
import Params from "../../tools/param";
import HocLink from "../../component/link";
import { connect } from "dva";
import moment from "moment-timezone";
import { Toast } from "antd-mobile";
class Index extends Component {
  constructor(props) {
    super(props);
    // 从url中解析出storeId
    let orderId = Params.get("orderId", window.location.href);
    this.state = {
      hasClick: false,
      orderId,
      tableNumber: localStorage.tableNumber,
      storeId: localStorage.storeId,
      orderInfo: {},
      email: ""
    };
  }
  componentWillMount() {
    this.props.showLoading();
    this.transactionsOrder({ orderId: this.state.orderId });
    // 获取订单详情
    this.orderDetail({
      orderId: this.state.orderId
    });
    // 获取抽奖的金额
    this.luckyDraw();
    // 店铺信息
    this.getBusinessInfo(this.state.storeId);
  }
  // 订单支付记录
  transactionsOrder = params => {
    this.props
      .dispatch({
        type: "global/transactionsOrder",
        payload: {
          ...params
        }
      })
      .then((paymentList = []) => {
        this.setState({
          paymentList
        });
      });
  };
  luckyDraw() {
    this.props
      .dispatch({
        type: "global/luckyDraw"
      })
      .then((data = {}) => {
        this.setState({
          luckyAmount: data.amount || 0
        });
      });
  }
  // 获取商家信息
  getBusinessInfo(storeId) {
    this.props.dispatch({
      type: "orders/getBusinessInfo",
      payload: {
        storeId
      }
    });
  }
  //获取付款后的订单
  orderDetail = params => {
    this.props
      .dispatch({
        type: "orders/orderDetail",
        payload: {
          ...params
        }
      })
      .then(data => {
        this.props.hideLoading();
        this.setState({
          orderInfo: data
        });
      });
  };

  //监听email
  watchEmail = e => {
    let { value = "" } = e.target;
    let emailRex = /\w+@[a-z0-9]+\.[a-z]{2,4}/;
    if (emailRex.test(value)) {
      this.setState({
        canSubmit: true
      });
    } else {
      this.setState({
        canSubmit: false
      });
    }
    if (value.length > 0) {
      this.setState({
        email: value,
        showCloseIcon: true
      });
    } else {
      this.setState({
        email: "",
        showCloseIcon: false
      });
    }
  };
  //清除email
  clearPhone = () => {
    this.setState({
      email: "",
      canSubmit: false,
      showCloseIcon: false
    });
  };
  //切换modal
  toggleModal = () => {
    this.setState({
      visible: !this.state.visible
    });
  };
  componentDidMount() {
    let _this = this;
    setTimeout(() => {
      luckyCard(
        {
          ratio: 0.4
        },
        function() {
          _this.toggleModal();
          this.clearCover();
        }
      );
    }, 100);
  }
  // 领取奖励
  onOk = () => {
    this.props.showLoading();
    this.props
      .dispatch({
        type: "global/emailBind",
        payload: {
          amount: this.state.luckyAmount,
          email: this.state.email
        }
      })
      .then(res => {
        this.props.hideLoading();
        if (res.code === 200) {
          Toast.info("Sent", 2);
        } else {
          Toast.info("Error", 2);
        }
      });
  };

  render() {
    const { orderInfo, luckyAmount = 0 } = this.state;
    const { businessInfo = {} } = this.props.orders;
    let paymentList = this.state.paymentList || [];
    let footer = undefined;
    // 已经评价
    if (orderInfo.state === "COMPLETED") {
      footer = (
        <div>
          <span
            onClick={this.props.hocLink(
              `#/orderCheck?orderId=${this.state.orderId}`
            )}
            className={Styles.sep_btn}
          >
            My Order
          </span>
        </div>
      );
    } else {
      footer = (
        <div className={Styles.btn_list}>
          <span
            onClick={this.props.hocLink(
              `#/evaluate?orderId=${this.state.orderId}`
            )}
          >
            Rate Now
          </span>
          <span
            onClick={this.props.hocLink(
              `#/orderCheck?orderId=${this.state.orderId}`
            )}
            className={Styles.active}
          >
            My Order
          </span>
        </div>
      );
    }
    return (
      <View title="Payment Successful" footer={footer}>
        {orderInfo.isOfflinePay === 1 ? (
          <div className={Styles.offlinepayment}>
            The staff has been notified and will come to yout table shortly.
            <br />
            Please wait.
          </div>
        ) : (
          <div className={Styles.payment_icon}>
            <span>Payment Successful</span>
          </div>
        )}

        {/* 标题 */}
        <div className={Styles.title}>
          <p>{businessInfo.storeName}</p>
          <p>${orderInfo.totalAmount}</p>
        </div>
        {/* 是否可以刮奖 */}
        <div className={Styles.can_lottery}>
          <div>
            <p>Congrats! </p>
            <p>You've got a chance to win a sweepstake!</p>
            <div className={Styles.can_lottery_section}>
              <div id="scratch" className={Styles.scratch}>
                {!this.state.hasClick && (
                  <span
                    onClick={() => {
                      this.setState({ hasClick: true });
                    }}
                  >
                    Click here to start
                  </span>
                )}
                <div id="card" className={Styles.scratch_card}>
                  ${luckyAmount}
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* 订单信息 */}
        <div className={Styles.order_ino}>
          {paymentList.length > 0 && (
            <Line
              label="Transaction ID:"
              content={paymentList[paymentList.length - 1].transactionId}
            />
          )}
          <Line
            label="Time:"
            content={moment(orderInfo.lastUpdatedAt)
              .utcOffset(1)
              .format("HH:mm:ss DD-MM-YYYY")}
          />
        </div>
        {/* 中奖之后在展示email输入框 */}
        {this.state.luckyAmount && (
          <div className={Styles.enter}>
            <p>Send receipt to email</p>
            <div className={Styles.email}>
              <input
                type="text"
                value={this.state.email}
                onChange={this.watchEmail}
                placeholder="Your email address"
              />
              {this.state.showCloseIcon ? (
                <i className={Styles.field_close} onClick={this.clearPhone} />
              ) : (
                <i className={Styles.field_edit} />
              )}
              {this.state.canSubmit ? (
                <span className={Styles.active} onClick={this.onOk}>
                  Confirm
                </span>
              ) : (
                <span>Confirm</span>
              )}
            </div>
          </div>
        )}

        {/* 中奖提示框 */}
        {this.state.visible && (
          <Modal
            title="Congrats!"
            close={this.toggleModal}
            hideSeconds={true}
            btnText="Download Now"
          >
            <div className={Styles.gift_card}>
              <span>You won a ${luckyAmount} gift card!</span>
              <span>Download our app to use it.</span>
            </div>
          </Modal>
        )}
      </View>
    );
  }
}

export default connect(state => {
  return {
    orders: state.orders
  };
})(HocLink(Index));
