import { addCustomercard } from "../../server";
export default {
  namespace: "bankCard",
  state: {},
  effects: {
    // 设置商家信息
    *addbankCard({ payload }, { call, put }) {
      try {
        yield call(addCustomercard, {
          ...payload
        });
      } catch (e) {
        console.log(e);
      }
    }
  },
  reducers: {}
};
